/*! Flickity v1.1.1
http://flickity.metafizzy.co
---------------------------------------------- */
.carousel{
  margin-bottom: 1em;
  //margin-right: -5%;
}

.carousel.gallery{
  margin-bottom: 2em;
}

.carousel.gallery-text{
  width: 100%;
  & .gallery-cell{
    margin-bottom: 2em;
  }
}

.circle-list{
  & > .js-flickity{
    padding-bottom: 1em;
    margin-bottom: 2em;
  }
}

.before-after{
  & .flickity-page-dots{
    margin: 0.5em 0;
    padding-bottom: 2em;
  }
}

.gallery-cell, .grid-item, .tested-item, .transformation{
  width: 90%;
  margin-right: 5px;
  //left: -5% !important;
  & img{
    width: 100%;
  }
}

.grid{
  margin-bottom: 1em;
}

.headliner{
  position: relative;
}

.headliner-cell.is-selected{
  opacity: 1;
}

.headliner-cell{
  width: 90% !important;
  opacity: 0.05;
  background-color: #000;
}

.flickity-enabled {
  position: relative;
}

.flickity-enabled:focus { outline: none; }

.flickity-viewport {
  overflow: hidden;
  position: relative;
  height: 100%;
}

.tried-n-tested .flickity-viewport{
  margin-bottom: -9em;
}

.flickity-slider {
  position: absolute;
  width: 100%;
  height: 100%;
}

/* draggable */

.flickity-enabled.is-draggable {
  -webkit-tap-highlight-color: transparent;
          tap-highlight-color: transparent;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.flickity-enabled.is-draggable .flickity-viewport {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

/* ---- previous/next buttons ---- */

.flickity-prev-next-button {
  display: none;
}

.headliner > .flickity-prev-next-button {
  display: block;
  position: absolute;
  top: 50%;
  width: 10%;
  height: 1em;
  margin: 0;
  border: none;
  border-radius: 0;
 // background: rgba($slate, 0.8);
  height: 100%;
  cursor: pointer;
  /* vertically center */
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  &:focus {
    outline: none;
    box-shadow: 0 0 0 5px #09F;
  }
  &.previous { 
    display: none;
    left: 0em;
    & svg{
      left: 0;
    }
  }
  &.next { 
    right: 0;
    & svg{
      left: 68%;
    }
  }
  /* right to left */
  .flickity-rtl &.previous {
    left: auto;
    right: 10px;
  }
  .flickity-rtl &.next {
    right: auto;
    left: 10px;
  }
  & svg {
    position: absolute;
    top: 20%;
    width: 20%;
    height: 60%;
  }

  & .arrow {
    fill: #FFF;
  }

  /* color & size if no SVG - IE8 and Android 2.3 */
  &.no-svg {
    color: #FFF;
    font-size: 26px;
  }
}

/* ---- page dots ---- */

.gallery .flickity-page-dots, .no-dots .flickity-page-dots {
  display: none;
}

.no-dots.grid{
  margin-bottom: 0;
}

.flickity-page-dots {
  position: relative;
  width: 100%;
  bottom: -1em;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  line-height: 2;
}

.flickity-rtl .flickity-page-dots { direction: rtl; }

.flickity-page-dots .dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 0.3em;
  background: $slate-lt;
  border-radius: 50%;
  filter: alpha(opacity=25); /* IE8 */
  opacity: 0.25;
  cursor: pointer;
}

.dark .dot, .videos .dot{
  background: $clay-blue;
}

.flickity-page-dots .dot.is-selected {
  filter: alpha(opacity=100); /* IE8 */
  opacity: 1;
}

@media (max-width: $mq-s){
  .flickity-page-dots .dot {
    width: 7px;
    height: 7px;
    margin: 0 0.2em;
  }
}